.container {
  max-width: 1230px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.btn {
  text-transform: uppercase;
  letter-spacing: 4px;
}

.btn--line {
  border-bottom: 2px solid rgba(217, 121, 142, 0.75);
  position: relative;
}

.btn--line::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: -2px;
  width: 0;
  height: 2px;
  background: #d12e2e;
  transition: .3s linear;
  box-shadow: 0 3px 25px #d12e2e;
}

.btn--line:hover {
  color: #d12e2e;
}

.btn--line:hover::after {
  width: 100%;
}

.header {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 10;
  transition: .25s linear;
}

.header.is-scroll {
  background-color: #1b1e24;
}

.header.is-scroll .logo__main {
  max-width: 120px;
}

@media only screen and (max-width: 980px) {
  .header.is-scroll .logo__main {
    max-width: 85px;
  }
}

.header.is-scroll .logo__image {
  transform: scale(0.9);
}

@media only screen and (max-width: 980px) {
  .header.is-scroll .logo__image {
    transform: scale(1);
  }
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 0;
  padding: 7px 0;
}

.header .menu {
  flex: 0 33.3%;
}

.header .menu__btn {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 34px;
  height: 30px;
}

.header .menu__btn__line {
  display: block;
  width: 30px;
  background-color: #fff;
  height: 2px;
  margin: 2px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  transition: .2s ease;
  position: relative;
}

.header .menu__btn__line--small {
  width: 60%;
}

.header .menu__btn__line::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 0;
  background-color: #D12E2E;
  transition: .2s ease;
}

.header .menu__btn:hover .menu__btn__line::after {
  width: 100%;
}

.header .menu__btn:hover .menu__btn__line--small {
  width: 30px;
}

.header .menu__main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  margin-left: -100%;
  padding-top: 10px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-blend-mode: saturation;
  background-color: rgba(209, 46, 46, 0.8);
  z-index: 10;
  transition: .3s ease;
  text-align: center;
}

.header .menu__main.is-active {
  display: block;
  margin: 0;
}

.header .menu__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
}

.header .menu__item {
  margin: 10px 0;
}

.header .menu__link {
  font-size: 30px;
  color: #fff;
  line-height: 40px;
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Myriad Pro", sans-serif;
  letter-spacing: 5px;
  transition: .3s linear;
}

@media only screen and (max-width: 980px) {
  .header .menu__link {
    font-size: 25px;
  }
}

.header .menu__link:hover {
  color: #D12E2E;
}

.header .menu__close {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 35px;
  height: 35px;
  position: absolute;
  top: 20px;
  right: 30px;
  background: none;
}

@media only screen and (max-width: 1190px) {
  .header .menu__close {
    top: 10px;
    right: 10px;
  }
}

.header .menu__close__line {
  display: inline-block;
  height: 3px;
  width: 35px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  transform: rotate(45deg);
  background-color: #fff;
  position: relative;
  transition: .3s ease;
}

.header .menu__close__line::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  height: 3px;
  width: 35px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  transform: rotate(90deg);
  background-color: #fff;
  transition: .3s ease;
}

.header .menu__close:hover .menu__close__line,
.header .menu__close:hover .menu__close__line::before {
  background-color: #D12E2E;
}

.header .logo {
  flex: 0 33.3%;
  text-align: center;
}

.header .logo__image {
  width: 100%;
  transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1) 0s;
}

.header .logo__main {
  display: inline-block;
  max-width: 140px;
  width: 100%;
  transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1) 0s;
}

.header .logo__menu {
  display: inline-block;
  max-width: 140px;
}

.header .logo__menu__img {
  width: 100%;
}

.header .contact {
  flex: 0 33.3%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.header .contact__phone {
  font-size: 14px;
  font-family: "Myriad Pro", sans-serif;
  color: white;
  line-height: 18px;
  margin-bottom: 5px;
  transition: .3s ease;
  letter-spacing: 1px;
}

@media only screen and (max-width: 440px) {
  .header .contact__phone {
    letter-spacing: 0;
  }
}

.header .contact__phone:hover {
  color: #D12E2E;
}

.header .contact__email {
  font-size: 9px;
  font-family: "GothamPro", sans-serif;
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  line-height: 10px;
  transition: .3s ease;
}

.header .contact__email:hover {
  color: #D12E2E;
}

.modal {
  position: fixed;
  z-index: 20;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100%;
  padding: 10px;
  visibility: hidden;
  opacity: 0;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.4);
  overflow-y: scroll;
}

.modal__content {
  margin: auto;
  max-width: 1200px;
  width: 100%;
  padding: 80px 10px;
  position: relative;
  background-color: #fff;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}

.modal__close {
  position: absolute;
  width: 40px;
  height: 40px;
  padding: 5px;
  right: 10px;
  top: 10px;
  cursor: pointer;
  transition: .2s linear;
}

.modal__close::before {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 60%;
  height: 2px;
  background-color: #d12e2e;
  transform: translate(-50%, -50%) rotate(135deg);
}

.modal__close::after {
  content: '';
  display: block;
  position: absolute;
  right: 50%;
  top: 50%;
  width: 60%;
  height: 2px;
  background-color: #d12e2e;
  transform: translate(50%, -50%) rotate(-135deg);
}

.modal__close:hover {
  transform: rotate(180deg);
  transform-origin: center;
}

.modal.is-active {
  display: flex;
  align-items: center;
  opacity: 1;
  visibility: visible;
}

.modal.is-active .modal__content {
  animation: slideIn .3s;
}

.modal .catalog__item {
  max-width: 270px;
}

.modal .catalog__wrap {
  max-width: 1000px;
  margin: auto;
}

@keyframes slideIn {
  from {
    bottom: -300px;
  }
  to {
    bottom: 0;
  }
}

.intro {
  display: flex;
  align-items: center;
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgba(209, 46, 46, 0.8);
  background-blend-mode: hard-light;
  height: 100vh;
  width: 100%;
  position: relative;
}

.intro__wrap {
  text-align: center;
}

.intro::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 55%;
  z-index: 1;
  background: -moz-linear-gradient(89deg, rgba(255, 255, 255, 0) 0%, black 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, black), color-stop(100%, rgba(255, 255, 255, 0)));
  background: -webkit-linear-gradient(89deg, rgba(255, 255, 255, 0) 0%, black 100%);
  background: -o-linear-gradient(89deg, rgba(255, 255, 255, 0) 0%, black 100%);
  background: -ms-linear-gradient(89deg, rgba(255, 255, 255, 0) 0%, black 100%);
  background: linear-gradient(1deg, rgba(255, 255, 255, 0) 0%, black 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#ffffff',GradientType=0 );
}

.intro__title {
  padding: 20px;
  font-size: 36px;
  font-family: "Myriad Pro", sans-serif;
  color: white;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 7px;
  position: relative;
  overflow: hidden;
  z-index: 3;
}

@media only screen and (max-width: 1100px) {
  .intro__title {
    font-size: 26px;
  }
}

@media only screen and (max-width: 440px) {
  .intro__title {
    font-size: 24px;
    letter-spacing: 5px;
  }
}

.intro .btn {
  display: inline-block;
  margin-top: 30px;
  padding: 20px 80px 20px 50px;
  background-color: #D12E2E;
  color: #fff;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  position: relative;
}

.intro .btn__text {
  font-size: 12px;
  font-family: "Myriad Pro", sans-serif;
  position: relative;
  letter-spacing: 5px;
  font-weight: 700;
  z-index: 2;
  transition: .3s ease;
}

@media only screen and (max-width: 440px) {
  .intro .btn__text {
    letter-spacing: 3px;
  }
}

@media only screen and (max-width: 440px) {
  .intro .btn {
    padding: 15px 60px 15px 29px;
  }
}

.intro .btn__arrow {
  display: flex;
  align-items: center;
  margin: 0 5px;
  position: absolute;
  right: 25px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

.intro .btn__arrow .icon-arr-right {
  width: 40px;
  height: 20px;
  fill: #fff;
  transition: .3s ease;
}

@media only screen and (max-width: 440px) {
  .intro .btn__arrow {
    right: 10px;
  }
}

.intro .btn::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  background-color: #fff;
  transition: .3s ease;
  z-index: 1;
}

.intro .btn:hover .btn__text {
  color: #D12E2E;
}

.intro .btn:hover .btn__arrow .icon-arr-right {
  fill: #D12E2E;
  transform: translateX(10px);
}

.intro .btn:hover:after {
  width: 100%;
  color: #D12E2E;
}

.catalog {
  background-color: #fdfefe;
  padding: 70px 0 50px;
}

@media only screen and (max-width: 980px) {
  .catalog {
    padding-top: 50px;
  }
}

@media only screen and (max-width: 510px) {
  .catalog {
    padding-top: 30px;
  }
}

.catalog__wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}

@media only screen and (max-width: 740px) {
  .catalog__wrap {
    justify-content: space-around;
  }
}

.catalog__item {
  flex: 0 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 310px;
  height: 100%;
  font-size: 14px;
  font-family: "GothamPro", sans-serif;
  color: #181a1e;
  line-height: 1.84;
  text-align: center;
}

@media only screen and (max-width: 980px) {
  .catalog__item {
    max-width: 230px;
    font-size: 13px;
  }
}

@media only screen and (max-width: 740px) {
  .catalog__item {
    margin-bottom: 50px;
  }
}

.catalog__item__slider {
  width: 100%;
  padding-bottom: 30px;
}

.catalog__item__title {
  text-transform: uppercase;
}

.catalog__item__price {
  color: #D12E2E;
}

.catalog__item .slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 10px;
}

.catalog__item .slick-dots li {
  margin: 0 4px;
}

.catalog__item .slick-dots li.slick-active button:before {
  background: #d12e2e;
  width: 8px;
  height: 8px;
}

.catalog__item .slick-dots li button:before {
  background: rgba(205, 66, 69, 0.8);
  width: 6px;
  height: 6px;
}

.catalog__item__more {
  display: inline-block;
  margin-top: 20px;
  padding: 0 5px 5px;
  font-size: 12px;
  font-family: "Myriad Pro", sans-serif;
  color: #1B1E24;
  font-weight: 700;
  transition: .3s linear;
}

